/* COLOR PALETTE
#f0e4e4 
#140808
#00222b
#01bce175;
#02c0ec
#ff718a
#be2eaa
#fd8000
*/

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Source Sans Pro", sans-serif, "Playfair Display", "Roboto";
}

/* GENERAL */
html {
  -webkit-text-size-adjust: 100%;
}

body {
  background-color: #f1f0f5;
  color: #140808;
  line-height: 1.5;
}
.line-under {
  width: 8%;
  height: 8px;
  border-radius: 1rem;
  background: #02c0ec;
  margin-left: 46%;
  margin-bottom: 5rem;
}

/* HERO SECTION */
.hero-section {
  position: relative;
  text-align: center;
}

.hero-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  /* max-width: 90rem; */
  width: 92%;
}

.heading-primary {
  text-transform: uppercase;
  font-size: 5.5rem;
  letter-spacing: 0.2rem;
  color: #f0e4e4;
  text-align: center;
  font-family: "Secular One";
}

.hero-info {
  color: #f0e4e4;
  margin: 2rem auto 0 auto;
  font-size: 2.2rem;
  text-align: center;
  background-color: #01bce175;
  letter-spacing: 2px;
}

#banner-custom {
  height: 50rem;
  width: 100vw;
  z-index: 99;
}

/* SOCIALS */
.rightSide-socials {
  background: #002f3b;
  padding: 1rem;
  position: absolute;
  bottom: -250rem;
  border-radius: 0 10px 10px 0;
  box-shadow: 1px 3px 2.5px rgba(0, 0, 0, 0.034);
}

.individual-social {
  width: 4rem;
}

.social-link {
  text-decoration: none;
  color: #f0e4e4;
  width: 100%;
  display: block;
  transition: 0.4s;
}

ion-icon {
  font-size: 50px;
}

a:hover,
a:active,
a:focus {
  color: #02c0ec;
}
/* ABOUT ME */
.section-title {
  display: block;
  font-size: 3rem;
  text-transform: uppercase;
  margin-top: 5rem;
  color: #00222b;
  font-weight: 800;
  letter-spacing: 3px;
  font-family: "Source Sans Pro";
  text-align: center;
}

.logo-custom {
  float: left;
  width: 25rem;
}

#about-text {
  font-family: "Source Sans Pro";
  font-size: 1.4rem;
  line-height: 1.5;
}

/* SKILLS */
.skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
#skill-specific {
  padding: 1rem 2rem;
  margin: 0 1.5rem 1.5rem 0;
  font-size: 1.3rem;
  background: #ff718a;
  border-radius: 1.2rem;
  font-weight: 500;
  color: #f0e4e4;
}

/* PROJECTS */

#projects-overall {
  margin: 0;
  padding: 0;
}

.project-row {
  display: grid;
  grid-template-columns: 1.5fr 1fr;
  grid-gap: 5rem;
  margin-bottom: 10rem;
}

.projects-img {
  overflow: hidden;
}

#project-custom {
  width: 100%;
  display: block;
  object-fit: cover;
}

.projects-content {
  padding: 2rem 0;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
}

.project-title {
  font-weight: 700;
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
  font-family: "Playfair Display";
  letter-spacing: 1px;
}

.project-description {
  font-size: 1.3rem;
  font-family: "Source Sans Pro";
}

.btn-project {
  color: #f0e4e4;
  font-weight: 600;
  background-color: #fd8000;
  padding: 1.2rem 3rem;
  font-size: 1.3rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  border: none;
  font-family: "Source Sans Pro";
  display: inline-block;
  margin-top: 0.5rem;
}

/* CONTACT */
.contact-title {
  color: #f0e4e4;
}
.contact-section {
  padding: 2rem 2rem 7rem 2rem;
  background-color: #00222b;
  text-align: center;
}
.container {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.subhead-contact {
  color: #02c0ec;
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 1rem;
}

.inputs {
  border: none;
  padding: 1rem 1.2rem;
  background: #fd8000;
  width: 100%;
  margin-bottom: 1rem;
  outline: none;
  border-radius: 8px;
}

.message {
  min-height: 15rem;
}

form ::placeholder {
  color: #f0e4e4;
  font-weight: 500;
  font-size: 1.2rem;
}

.button {
  width: 10rem;
  padding: 1rem 1.5rem;
  border-radius: 8px;
  font-weight: 700;
  font-size: 1.4rem;
  color: #f0e4e4;
  background-color: #ff718a;
  border: none;
  text-transform: uppercase;
  letter-spacing: 2px;
  display: inline-block;
}
